* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  transition: 0.2s;
}

.page {
  min-height: 100vh;
}

.active {
  color: #ffc600 !important;
}

.clear-float {
  clear: both;
}

.page .header {
  width: calc(100% - 70px);
  position: fixed;
  top: 0;
  right: 0;
  float: right;
  height: 80px;
  background-color: #2B2A2F;
  color: #ffffff;
}
.page .header .logo {
  display: none;
}
.page .header .mobile-menu {
  display: none;
}
.page .header ul {
  padding: 30px;
  margin-right: 30px;
  float: right;
}
.page .header ul li {
  display: inline;
  margin: 20px;
  cursor: pointer;
}
.page .header ul li a {
  color: #ffffff;
  text-decoration: none;
  font-weight: 600;
  font-size: 0.9em;
}
.page .header ul li a:hover {
  color: #ffc600;
}

@media screen and (max-width: 768px) {
  .page .header {
    width: 100%;
    float: none;
  }
  .page .header .logo {
    display: block;
  }
  .page .header .logo img {
    width: 50px;
    margin: 15px;
  }
  .page .header .mobile-menu {
    display: block;
    float: right;
    position: relative;
    top: -60px;
    right: 2%;
    cursor: pointer;
  }
  .page .header .mobile-menu:hover .bar {
    width: 55px !important;
    border: 1px solid #ffc600;
  }
  .page .header .mobile-menu .bar {
    display: block;
    border: 1px solid #ffffff;
    margin-bottom: 13px;
  }
  .page .header .mobile-menu .bar:first-child {
    width: 55px;
  }
  .page .header .mobile-menu .bar:nth-child(2) {
    width: 40px;
  }
  .page .header .mobile-menu .bar:nth-child(3) {
    width: 25px;
  }
  .page .header .bars-x {
    top: -45px;
  }
  .page .header .bars-x .bar {
    width: 55px !important;
    margin-bottom: 0;
  }
  .page .header .bars-x .bar:first-child {
    transform: rotateZ(45deg);
  }
  .page .header .bars-x .bar:nth-child(2) {
    display: none;
  }
  .page .header .bars-x .bar:nth-child(3) {
    transform: rotateZ(135deg);
  }
  .page .header ul {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 0;
    height: calc(100vh - 80px);
    margin-top: 80px;
    text-align: center;
    overflow: hidden;
    background-color: #2e2d33;
    padding: 0 !important;
  }
  .page .header ul li {
    display: block;
    margin-bottom: 8%;
  }
  .page .header .show {
    width: 100%;
  }
}
.page .page-content {
  width: calc(100% - 70px);
  float: right;
  min-height: 400px;
}

@media screen and (max-width: 768px) {
  .page .page-content {
    width: 100%;
    float: none;
  }
}
.sidebar {
  width: 70px;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  padding: 10px;
  background-color: #2E2D33;
  color: #ffffff;
  overflow: hidden;
}
.sidebar .logo img {
  width: 50px;
  margin: 10px 0 20px;
}
.sidebar .social ul {
  list-style: none;
  margin-top: 7em;
}
.sidebar .social ul li a {
  display: block;
  color: #ffffff;
  font-size: 1.5em;
  margin-bottom: 30px;
  border: 1px solid #ffffff;
  width: 85%;
  padding: 10px;
  text-align: center;
}
.sidebar .social ul li a:hover {
  color: #ffc600;
  border: 1px solid #ffc600;
}

@media screen and (max-width: 768px) {
  .sidebar {
    display: none;
  }
}
.page .footer {
  width: calc(100% - 70px);
  float: right;
  padding: 30px 10px;
  background-image: radial-gradient(#3f3f40 20%, transparent 20%), radial-gradient(#bdc3c7 20%, transparent 20%);
  background-color: #29282d;
  background-size: 7px 7px;
  display: flex;
}
.page .footer .left {
  flex: 1;
  position: relative;
}
.page .footer .left h3 {
  font-weight: 600;
  font-size: 4em;
  color: rgba(255, 255, 255, 0.1411764706);
  text-transform: uppercase;
  letter-spacing: 25px;
  position: absolute;
  bottom: 0;
  left: 40px;
  transform: rotate(270deg);
  transform-origin: 0 0;
}
.page .footer .middle,
.page .footer .right {
  flex: 2;
}
.page .footer .middle h3,
.page .footer .right h3 {
  color: #5bc9e0;
  margin-bottom: 40px;
  text-transform: uppercase;
  font-size: 0.9em;
}
.page .footer .middle p,
.page .footer .right p {
  color: #c3c3c3;
  font-size: 0.81em;
  margin-bottom: 15px;
  width: 90%;
}
.page .footer .middle {
  margin-right: 50px;
}
.page .footer .middle form {
  position: relative;
  width: 90%;
  margin-top: 55px;
}
.page .footer .middle form input[type=email] {
  position: absolute;
  width: 70%;
  top: 0;
  left: 0;
  padding: 11.5px;
  background-color: transparent;
  border: 1px solid #ffc600;
  font-weight: 400;
  font-size: 1em;
  color: #ffffff;
}
.page .footer .middle form input[type=email]:focus-visible {
  border: 1px solid #5bc9e0;
  border-radius: none;
  outline: none;
}
.page .footer .middle form input[type=submit] {
  position: absolute;
  width: 30%;
  top: 0;
  left: 70%;
  padding: 14px;
  background-color: #ffc600;
  color: #ffffff;
  border: none;
  cursor: pointer;
}
.page .footer .middle form input[type=submit]:hover {
  background-color: #fdd23d;
}
.page .footer .middle ul {
  margin-top: 150px;
  list-style: none;
  color: #bdc3c7;
}
.page .footer .middle ul li {
  margin-bottom: 30px;
}
.page .footer .middle ul li span i {
  color: #ffc600;
  font-size: 1.5em;
  width: 30px;
}
.page .footer .middle ul li span a {
  text-decoration: none;
  color: #ffffff;
}
.page .footer .middle ul li span a:hover {
  color: #ffc600;
}
.page .footer .middle ul li span.element {
  font-weight: 600;
  display: inline-block;
  width: 73px;
}
.page .footer .middle ul li span.description {
  color: #ffffff;
}
.page .footer .right ul {
  list-style: none;
  margin-top: 50px;
}
.page .footer .right ul li {
  margin-bottom: 50px;
}
.page .footer .right ul li span.element {
  color: #bdc3c7;
  font-weight: 600;
  display: inline-block;
  width: 120px;
}
.page .footer .right ul li span.description {
  color: #ffffff;
}
.page .footer .right ul li span.description a {
  text-decoration: none;
  color: #ffffff;
  border: 1px solid #ffffff;
  display: inline-block;
  text-align: center;
  width: 35px;
  height: 35px;
}
.page .footer .right ul li span.description a:hover {
  color: #ffc600;
  border: 1px solid #ffc600;
}
.page .footer .right ul li span.description a i {
  margin-top: 8px;
}
.page .footer-banner {
  float: right;
  background-color: #2B2A2F;
  color: #7f8c8d;
  width: calc(100% - 70px);
  padding: 10px;
  text-align: center;
  font-weight: 600;
  font-size: 0.9em;
  letter-spacing: 2px;
  text-transform: uppercase;
}
.page .footer-banner .copy {
  font-size: 1.1em;
  font-weight: bolder;
}

@media screen and (max-width: 768px) {
  .page .footer {
    width: 100%;
    float: none;
    display: block;
  }
  .page .footer .left {
    display: none;
  }
  .page .footer .middle,
.page .footer .right {
    margin: 20px 10px 20px 3%;
  }
  .page .footer .middle {
    margin-bottom: 50px;
  }
  .page .footer .middle p,
.page .footer .middle form,
.page .footer .middle ul {
    margin-left: 3%;
  }
  .page .footer .middle ul {
    margin-top: 60px;
  }
  .page .footer .middle form {
    position: unset;
  }
  .page .footer .middle form input[type=email],
.page .footer .middle form input[type=submit] {
    display: inline;
    position: unset;
  }
  .page .footer .middle form input[type=email] {
    width: 60%;
  }
  .page .footer .middle form input[type=submit] {
    margin-left: -4px;
    padding: 15px;
  }
  .page .footer .middle::after {
    content: "";
    width: 80%;
    display: block;
    border: 1px solid rgba(255, 255, 255, 0.1411764706);
  }
  .page .footer .right p,
.page .footer .right ul {
    margin-left: 3%;
  }
  .page .footer-banner {
    width: 100%;
    float: none;
  }
}
.order-banner {
  width: calc(100% - 70px);
  float: right;
  border-top: 15px solid #2B2A2F;
  border-bottom: 15px solid #2B2A2F;
  background: linear-gradient(90deg, #2e2d33 50%, #36353b 50%);
  display: flex;
}
.order-banner .heading {
  flex: 3;
  height: 160px;
  display: flex;
  align-items: center;
}
.order-banner .heading h3 {
  color: #7f8c8d;
  background-color: #36353b;
  width: 50%;
  padding: 15px;
  border-radius: 2px;
  text-transform: uppercase;
  font-size: 0.8em;
  letter-spacing: 2px;
  font-weight: bolder;
  overflow: visible;
  white-space: nowrap;
}
.order-banner .heading h3 span {
  margin-left: 50%;
}
.order-banner .heading h3 span::before {
  content: "";
  display: inline-block;
  width: 60px;
  height: 2px;
  background: #ffc600;
  margin: 0 20px 4px -110px;
}
.order-banner .button {
  flex: 2;
  height: 160px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.order-banner .button a {
  text-decoration: none;
  width: 50%;
  height: 50px;
  background-color: #ffc600;
  color: #ffffff;
  border-radius: 2px;
  text-align: center;
}
.order-banner .button a:hover {
  background-color: #fdd23d;
  border-radius: 4px;
}
.order-banner .button a:hover span {
  letter-spacing: 3px;
}
.order-banner .button a span {
  line-height: 49px;
  text-transform: uppercase;
  font-size: 0.9em;
  font-weight: 600;
  letter-spacing: 1px;
}

#toTop {
  position: fixed;
  top: 87%;
  right: 3%;
  background-color: #ffc600;
  width: 45px;
  height: 60px;
  cursor: pointer;
  border-radius: 2px;
}
#toTop i {
  color: #ffffff;
  display: flex;
  justify-content: center;
  line-height: 55px;
  font-size: 1.2em;
}

@media screen and (max-width: 768px) {
  .order-banner {
    width: 100%;
    float: none;
    display: block;
  }
  .order-banner .button {
    height: 70px;
    justify-content: left;
    align-items: unset;
  }
  .order-banner .button a {
    width: 30%;
  }
}